import React from 'react';
import './App.css';
import ReactPageScroller from "react-page-scroller";
import TopPageComponent from "./components/top-page/top-page.component";
import ComingSoonComponent from "./components/comginsoon/coming-soon.component";
import ExperienceComponent from "./components/experience/experience.component";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import {withStyles} from '@material-ui/core/styles';
import PropTypes from 'prop-types';

import MenuIcon from '@material-ui/icons/Menu';
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import clsx from "clsx";

// import 'bootstrap/dist/css/bootstrap.min.css';

const useStyles = theme => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    list: {
        width: 400,
        maxWidth: '60vw',
        paddingLeft: '5%'
    },
});

// TODO: Add icons in pages[i][1]
const pages = [
    ['Home', ''],
    ['Experience', ''],
    ['Projects', ''],
    ['Education', ''],
    ['Skills', '']
]

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currPage: 0,
            drawerOpen: false
        }
    }

    list = () => (
        <div
            className={clsx(this.props.classes.list)}
            role="presentation"
        >
            <List>
                {pages.map((text, index) => (
                    <ListItem button key={text}>
                        <ListItemIcon>{text[1]}</ListItemIcon>
                        <ListItemText primary={text && text[0]}
                        onClick={() => {
                        this.setState({drawerOpen: false});
                        this.setState({currPage: index})
                    }}/>
                    </ListItem>
                ))}
            </List>
            <Divider/>
        </div>
    )

    handlePageChange = number => {
        this.setState({currPage: number});
    }

    scrollDown = _ => {
        this.setState({currPage: this.state.currPage + 1})
    }

    setPage = page => {
        console.log("Going to", page)
        this.setState({currPage: page})
    }


    render() {
        const {classes} = this.props;

        return (
            <div className="App">
                <SwipeableDrawer
                    anchor={'left'}
                    open={this.state.drawerOpen}
                    onClose={() => this.setState({drawerOpen: false})}
                    onOpen={() => this.setState({drawerOpen: true})}
                >
                    {this.list()}
                </SwipeableDrawer>
                <AppBar position="absolute" style={{backgroundColor: "transparent"}}>
                    <Toolbar style={{backgroundColor: "transparent"}}>
                        <IconButton edge="start" onClick={() => this.setState({drawerOpen: true})}
                                    className={classes.menuButton} color="inherit" aria-label="menu">
                            <MenuIcon style={{fontSize: '2rem'}}/>
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            {/*{pages[this.state.currPage] && this.state.currPage > 0 && (pages[this.state.currPage][0])}*/}
                        </Typography>
                        <div className={'nav-links'}>
                        {pages.map((page, i)=>{
                            let color = "inherit"
                            if(i===this.state.currPage)
                                color = "primary"
                            return <Button onClick={_=>this.setState({currPage: i})} color={color}>{page[0]}</Button>
                        })}</div>

                    </Toolbar>
                </AppBar>
                <ReactPageScroller
                    pageOnChange={this.handlePageChange}
                    customPageNumber={this.state.currPage}
                >
                    <TopPageComponent scrollDown={this.scrollDown}/>
                    <ExperienceComponent/>
                    <ComingSoonComponent/>
                    <ComingSoonComponent/>
                </ReactPageScroller>
            </div>
        );
    }
}

App.propTypes = {
    classes: PropTypes.object.isRequired,
}

export default withStyles(useStyles)(App);