import React from "react";

export default class ExperienceItem extends React.Component {
    render() {
        return (
            <div className={'experience-item'}>
                <img
                    alt={''}
                    src={this.props.imageUrl}
                    style={{
                        boxShadow: this.props.open ? 'rgba(30, 144, 255, 0.71) 0px 0px 20px 10px' : null,
                    }}/>
                <div className={'experience-item-details'}>
                    <h4 style={{fontWeight: 'bolder', margin: '0'}}>{this.props.company}</h4>
                    <h5 style={{margin: 0}}>{this.props.position}</h5>
                    <p className={'text-muted'}>
                        {this.props.duration}
                    </p>
                </div>
            </div>
        )
    }
}