import React from "react";
import './styles.css'
import {Row} from "react-bootstrap";
import 'react-vertical-timeline-component/style.min.css';
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepConnector from '@material-ui/core/StepConnector';
import Linkify from 'react-linkify'
import GitHubIcon from "@material-ui/icons/GitHub";
import ExperienceItem from "./experience-item.component";

//Icons

export default class ExperienceComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            curr: 1,
            open: null,
            openDesc: false
        }
        this.timeout = null;
    }

    experiences = [
        {
            company: 'PICT ACM Student Chapter',
            position: 'Web developer',
            duration: 'Feb \'19 - Present',
            imageUrl: 'https://media-exp1.licdn.com/dms/image/C4D0BAQFe2g2J6oSrhg/company-logo_100_100/0?e=1599696000&v=beta&t=r0wQiZGM-KdPiHJXG_Mwg-GUpqPT7Msv4HxRnqt-rLQ',
            description: 'Active member of PASC( https://pict.acm.org/ ), which has been awarded best ACM India Student Chapter Award for 3 times. As a member, I attend and also deliver sessions on various topics and technologies. Few of my works include an MCQ Solving Platform developed for tech fest Pulzion\'19 along with other team members.'
        },
        {
            imageUrl: 'https://github.com/pnshiralkar/PICT-Website/blob/master/images/extracurricular/pictoreal.png?raw=true',
            company: 'Pictoreal',
            position: 'Technical Coordinator',
            duration: 'Jul \'19 - Present',
            description: 'Technical Coordinator at college student group Pictoreal. I had developed a website for this from scratch. I had also developed a QR code based voting platform for one of the events organized under this. https://github.com/pnshiralkar/QR-Code-Based-Voting-Platform'
        },
        {
            imageUrl: 'https://media-exp1.licdn.com/dms/image/C510BAQE4uMncKjsQww/company-logo_100_100/0?e=1599696000&v=beta&t=6x_iHj6Y9mfYHnUya2O9r2Q0CiscYPGOdgf_NQV1j7k',
            company: 'Interview Mocha',
            position: 'Project Intern',
            duration: 'Sept \'19 - Present',
            description: 'Working as an equal member of a two person team to develop an Online IDE which takes code and input from users, compiles and executes code and gives back output or error. Worked on technologies like Django, PostgreSQL, Docker and Azure VM. https://ide.interviewmocha.com. Also developing a Jupyterhub based online Data Science Simulator, deployed on a Kubernetes cluster.'

        },
        {
            imageUrl: 'https://media-exp1.licdn.com/dms/image/C4E0BAQFd0YbG4efCfg/company-logo_100_100/0?e=1599696000&v=beta&t=5n3IQYdM-5T3-TwkYYmhNmuC8P6ync4o27asyQJQ5rI',
            company: 'FOSSEE, IIT Bombay',
            position: 'Summer Fellow',
            duration: 'April - June \'20',
            description: 'Contributed to an Open Source Project which is a platform where instructors can book workshops proposed by coordinators. Being my first open-source contribution, this was a good starting point for my Open Source journey. Also got to know about some advanced Django concepts. https://github.com/FOSSEE/workshop_booking'

        }

    ]

    render() {
        return <div
            style={{overflowY: 'hidden', position: 'relative', width: '100vw', height: '100vh', background: 'black'}}>
            <Row style={{height: 'calc(70% - 90px)', width: '100%'}}>
                <div className={'github-contri-wrapper'}>
                    <Stepper activeStep={1} connector={<StepConnector/>}
                             style={{background: '#0000', marginTop: '50px', position: 'relative'}}>
                        {this.experiences.map((exp, index) => {
                            return <Step key={index} onMouseOver={_ => {
                                this.setState({open: index, openDesc: true})
                                if (this.timeout)
                                    clearTimeout(this.timeout)
                            }}>
                                <StepLabel icon={<ExperienceItem {...exp} open={(this.state.open === index)}/>}/>
                            </Step>
                        })}

                    </Stepper>
                    <div className={'description'} style={{opacity: this.state.openDesc === false ? 0 : 1,}}
                         onMouseLeave={_ => {
                             this.setState({openDesc: false})
                             this.timeout = setTimeout(_ => {
                                 this.setState({open: null})
                             }, 2000)
                         }} onMouseOver={_ => {
                        this.setState({openDesc: true})
                        if (this.timeout)
                            clearTimeout(this.timeout)
                    }}>
                        {this.state.open !== null && (<div><h2>{this.experiences[this.state.open].company}</h2>
                            <h4>{this.experiences[this.state.open].position} | {this.experiences[this.state.open].duration}</h4>
                            <Linkify properties={{target: '_blank'}}>
                                <p>{this.experiences[this.state.open].description}</p></Linkify></div>)}
                    </div>
                    {this.state.open === null &&
                    <h2 style={{opacity: 0.2}}>{'ontouchstart' in window ? "Tap" : "Hover"} on an experience to know more!</h2>}
                </div>
            </Row>
            <Row className={'github-row'}>
                <div className={'github-contri-wrapper'}>
                    <h2 style={{verticalAlign: 'bottom'}}><GitHubIcon fontSize={"large"}/> Github Contributions</h2>
                    <img
                    className={'github-contributions'}
                    onClick={()=>{window.open('https://github.com/pnshiralkar', '_blank')}}
                    src={`https://ghchart.rshah.org/0077ff/pnshiralkar?abc=${(new Date()).toString()}`}
                    alt={''}/>

                </div>
            </Row>
        </div>
    }
}

